@media (prefers-reduced-motion: no-preference) {
    .Appo {
      -webkit-animation: marquee infinite 8s linear;
              animation: marquee infinite 8s linear;
    }
}

@-webkit-keyframes marquee {
    0%   { transform: translate(0, 0); -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in;}
    25% { transform: translate(-30%, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
    75% { transform: translate(+30%, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
}

@keyframes marquee {
    0%   { transform: translate(0, 0); -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in;}
    25% { transform: translate(-30%, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
    75% { transform: translate(+30%, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out;}
}

