@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');
@media (prefers-reduced-motion: no-preference) {
    .Appo {
      animation: marquee infinite 8s linear;
    }
}

@keyframes marquee {
    0%   { transform: translate(0, 0); animation-timing-function: ease-in;}
    25% { transform: translate(-30%, 0); animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); animation-timing-function: ease-out;}
    75% { transform: translate(+30%, 0); animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); animation-timing-function: ease-out;}
}

@media (prefers-reduced-motion: no-preference) {
    .Appor {
      animation: marqueer infinite 5s linear;
    }
}

@keyframes marqueer {
    0%   { transform: translate(0, 0); animation-timing-function: ease-in;}
    25% { transform: translate(+30%, 0); animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); animation-timing-function: ease-out;}
    75% { transform: translate(-30%, 0); animation-timing-function: ease-out;}
    50% { transform: translate(0, 0); animation-timing-function: ease-out;}
}
